import axios from "axios"

const url = process.env.NODE_ENV === 'production'
            ? '/api/pedidos/'                         // en producción
            : `http://localhost:${process.env.REACT_APP_PORT_SRV_DEV}/api/pedidos/`   // en desarrollo


export const enviar = async pedido => (await axios.post(url, pedido)).data

export async function getPreferenceId(carrito) {
    console.log(carrito)

    const prefItems = {
        body: {
            items: carrito.map(p => ({
                id: p._id,
                title: p.nombre,
                currency_id: "ARS",
                picture_url: p.foto,
                description: p.detalles,
                category_id: p.categoria,
                quantity: parseInt(p.cantidad),
                unit_price: +p.precio
            })),
            back_urls: {
                success: window.location.href,
                failure: window.location.href,
                pending: window.location.href
            },
            auto_return: "approved",
        }
    }

    const {data:preferenceId} = await axios.post(url + 'mp/create_preference', prefItems)
    return preferenceId
}
