export function Tabla(props) {
    const { carrito, decrementarItem, incrementarItem, borrarItem } = props     // Object destructuring

    return (
        <table>
            <thead>
                <tr>                    
                    <th>nombre</th>
                    <th>precio</th>
                    <th>marca</th>
                    <th>foto</th>
                    <th>cantidad</th>
                    <th>subtotal</th>
                    <th>acciones</th>
                </tr>
            </thead>
            <tbody>
                {
                    carrito.map((producto, index) =>
                        <tr key={index} className="centrar">                            
                            <td>{producto.nombre}</td>
                            <td >${producto.precio}</td>
                            <td>{producto.marca}</td>
                            <td><img width="150" src={producto.foto + '?' + (1 || Math.random())} alt={"foto de " + producto.nombre} /></td>
                            <td>
                                {producto.cantidad}
                                <button id={"btnDecrementar-" + producto.id} onClick={
                                    () => decrementarItem(producto.id)
                                }>-</button>
                                <button id={"btnIncrementar-" + producto.id} onClick={
                                    () => incrementarItem(producto.id)
                                }>+</button>
                            </td>
                            <td>
                                ${producto.precio * producto.cantidad}
                            </td>
                            <td>
                                <button id={"btnBorrar-" + producto.id} onClick={
                                    () => borrarItem(producto.id)
                                }>Borrar</button>
                            </td>
                        </tr>
                    )
                }

                <tr>
                    <td colSpan="7">
                        <h4>Total:  ${carrito.reduce((acc, p) => acc + p.precio * p.cantidad, 0)}</h4>
                    </td>
                </tr>
            </tbody>
        </table>
    )
}