import './Index.css'

export function Index() {
    return (
        <section className="nosotros">

            <div className="nosotros-historia">


                <h1>Nosotros</h1>

                <p>
                    Bienvenido a Diagon Alley, el hogar de los fanáticos de Harry Potter en todo el mundo.
                    Nuestra historia comenzó en 2008, en un momento de oscuridad y desafíos personales. En medio de una
                    depresión, descubrí que había una luz brillante en el mundo de la magia: Harry Potter.
                </p>
                <p>
                    Con cada página leída, cada hechizo pronunciado y cada aventura vivida junto a Harry, Hermione y Ron,
                    encontré fuerza, esperanza y motivación para enfrentar los obstáculos de la vida. Harry Potter no era solo una serie de libros, era un refugio, un compañero y un mundo en el que podía sumergirme para escapar de la realidad y encontrar inspiración.
                </p>
                <p> Fue entonces cuando decidí compartir mi pasión por Harry Potter con el mundo. Nació la idea de crear una
                    librería especializada en la distribución de los libros de esta increíble saga alrededor del mundo. Quería que otros fanáticos experimentaran la misma magia que había encontrado en esas páginas.
                </p>
                <p>
                    Así, en 2008, nació Diagon Alley. Desde entonces, nos hemos dedicado a proporcionar a los fanáticos de Harry Potter de todas partes una experiencia única. Ya sea que estés buscando la colección completa de libros, ediciones especiales, merchandising exclusivo o simplemente un lugar para compartir tu amor por el mundo mágico de J.K. Rowling, aquí encontrarás todo lo que necesitas.
                </p>
                <p>
                    En Diagon Alley, no solo vendemos libros; ofrecemos un lugar donde los fanáticos pueden conectarse,
                    compartir su entusiasmo y sumergirse en el mundo de la magia. Cada libro que enviamos lleva consigo una
                    historia de superación personal, de encontrar la luz en la oscuridad y de creer en la magia, tanto dentro como fuera de las páginas.
                </p>
                <p>
                    Únete a nuestra comunidad de fanáticos de Harry Potter y descubre un mundo de aventura, amistad y magia que trasciende las páginas de los libros. ¡Bienvenido a Diagon Alley, donde la magia nunca termina!
                </p>

            </div>
        </section>
    )
}
