import React, { useState } from 'react'

export function Contacto(props) {

    const { contacto, onChange, valido } = props
    const { nombre, email, comentario } = contacto

    const [errors, setErrors] = useState({})

    const validateEmail = (email) => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        return emailRegex.test(email)
    }

    const onBlur = (event) => {
        const { id, value } = event.target
        const newErrors = { ...errors }

        if (!value) {
            newErrors[id] = `*El campo ${id} es obligatorio`
        } else if (id === 'email' && !validateEmail(value)) {
            newErrors[id] = '*El email no es válido'
        } else {
            delete newErrors[id]
        }

        setErrors(newErrors)
    }

    return (
        <section className="contacto">
            <form className="contacto-form">

                <h1>Contacto</h1>

                <div className="contacto-group">
                    <label htmlFor="nombre">Nombre</label>
                    <input
                        id="nombre"
                        type="text"
                        name="nombre"
                        value={nombre}
                        onChange={onChange}
                        onBlur={onBlur}
                        required
                    />
                    <div className="error-details">{errors.nombre}</div>
                </div>

                <div className="contacto-group">
                    <label htmlFor="email">e-mail</label>
                    <input
                        id="email"
                        type="email"
                        name="email"
                        value={email}
                        onChange={onChange}
                        onBlur={onBlur}
                        required
                    />
                    <div className="error-details">{errors.email}</div>
                </div>

                <div className="contacto-group">
                    <label htmlFor="comentario">Comentario</label>
                    <input
                        id="comentario"
                        type="text"
                        name="comentario"
                        value={comentario}
                        onChange={onChange}
                        onBlur={onBlur}
                        required
                    />
                    <div className="error-details">{errors.comentario}</div>
                </div>


                <button disabled={!valido} className="btnAgregar">
                    Enviar
                </button>
            </form>
        </section>
    )
}