export function Tabla(props) {
        
    const { productos, editar, borrar, editarID } = props     // Object destructuring
    
    return (
        <>
        <div className="alta-tabla">
        <h2>Lista de productos disponibles</h2>
        { productos.length === 0
            ? <h2 className="msg-error">No se encontraron productos para mostrar</h2>
            : <table>
                <thead>
                    <tr>                        
                        {/* <th>ID</th> */}
                        <th>nombre</th>
                        <th>precio</th>
                        <th>stock</th>
                        <th>marca</th>
                        <th>categoria</th>
                        <th>descripción corta</th>
                        <th>descripción larga</th>                      
                        <th>foto</th>
                        <th>envío</th>
                        <th>acciones</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        productos.map((producto,index) => 
                            <tr key={index}>
                                {/* <td className="centrar">{producto.id}</td> */}
                                <td className="centrar">{producto.nombre}</td>                                
                                <td className="centrar">{producto.precio}</td>
                                <td className="centrar">{producto.stock}</td>
                                <td className="centrar">{producto.marca}</td>
                                <td className="centrar">{producto.categoria}</td>
                                <td className="centrar">{producto.descripcionCorta}</td>  
                                <td className="centrar">{producto.descripcionLarga}</td> 

                                <td>
                                    <img 
                                        width="150" 
                                        src={producto.foto + '?' + (1 || Math.random())} 
                                        alt={"foto de " + producto.nombre} 
                                    />
                                </td>
                                <td className="centrar">{producto.envio? 'Si' : 'No'}</td>
                                <td>
                                    <button id={"btnBorrar-"+producto.id} onClick={
                                        () => borrar(producto.id)
                                    }>Borrar</button>

                                    <button id={(editarID && editarID === producto.id? "btnCancelar-" : "btnEditar-")+producto.id} onClick={
                                        () => editar(producto.id)
                                    }>{editarID && editarID === producto.id? 'Cancelar':'Editar'}</button>
                                </td>                            
                            </tr>
                        )
                    }
                </tbody>
            </table>
        }
        </div>
        </>
    )
}