import { useState } from 'react'
import './Index.css'
import { Contacto } from './Contacto'


export function Index() {
    const [contacto, setContacto] = useState({
        nombre: '', 
        email:'', 
        comentario: ''
    })

    function formularioValido() {
        for (let campo in contacto) {
            if (!contacto[campo]) {
                return false
            }
        }

        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        if (!emailRegex.test(contacto.email)) {
            return false
        }

        return true
    }

    function onChange(e) {
        const { id, type, value, checked } = e.target
        const contactoClon = { ...contacto }
        contactoClon[id] = type === 'checkbox' ? checked : value
        setContacto(contactoClon)
    }


    return (
        <section className="contacto">
            
            <Contacto 
                contacto={contacto}  
                onChange={onChange}
                valido={formularioValido()}
            />
        </section>
    )
    
}
