import React, { useState } from 'react'
import { ObtenerFoto } from "./ObtenerFoto"

export function Ingreso(props) {
    const {
        nombre,
        precio,
        stock,
        marca,
        categoria,
        descripcionCorta,
        descripcionLarga,
        edadDesde,
        edadHasta,
        foto,
        envio,
    } = props.producto
    const { onChange, onSubmit, valido, editarID, escribirCampoUrlFoto } = props

    const [errors, setErrors] = useState({})

    const onBlur = (event) => {
        const { id, value } = event.target

        const newErrors = { ...errors }

        if (!value) {
            newErrors[id] = `*El campo ${id} es obligatorio`
        } else {
            delete newErrors[id]
        }

        setErrors(newErrors)
    }

    return (        
            <form className="alta-form" onSubmit={onSubmit}>

                <h1>Alta de productos</h1>

                <div className="input-group">
                    <label htmlFor="nombre">nombre</label>
                    <input
                        className="entrada"
                        id="nombre"
                        type="text"
                        name="nombre"
                        value={nombre}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.nombre}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="precio">precio</label>
                    <input
                        className="entrada"
                        id="precio"
                        type="number"
                        step="0.00"
                        name="precio"
                        value={precio}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.precio}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="stock">stock</label>
                    <input
                        className="entrada"
                        id="stock"
                        type="number"
                        name="stock"
                        value={stock}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.stock}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="marca">marca</label>
                    <input
                        className="entrada"
                        id="marca"
                        type="text"
                        name="marca"
                        value={marca}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.marca}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="categoria">categoría</label>
                    <input
                        className="entrada"
                        id="categoria"
                        type="text"
                        name="categoria"
                        value={categoria}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.categoria}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="descripcionCorta">descripción corta</label>
                    <input
                        className="entrada"
                        id="descripcionCorta"
                        type="text"
                        name="descripcionCorta"
                        value={descripcionCorta}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.descripcionCorta}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="descripcionLarga">descripción larga</label>
                    <input
                        className="entrada"
                        id="descripcionLarga"
                        type="text"
                        name="descripcionLarga"
                        value={descripcionLarga}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.descripcionLarga}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="edadDesde">edad desde</label>
                    <input
                        className="entrada"
                        id="edadDesde"
                        type="number"
                        name="edadDesde"
                        value={edadDesde}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.edadDesde}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="edadHasta">edad hasta</label>
                    <input
                        className="entrada"
                        id="edadHasta"
                        type="number"
                        name="edadHasta"
                        value={edadHasta}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.edadHasta}</div>
                </div>

                <div className="input-group">
                    <label htmlFor="foto">foto</label>
                    <input
                        className="entrada"
                        id="foto" type="text"
                        name="foto" value={foto}
                        onChange={onChange}
                        onBlur={onBlur}
                    />
                    <div className="error-details">{errors.foto}</div>
                </div>

                <ObtenerFoto escribirCampoUrlFoto={escribirCampoUrlFoto} />

                <div className="input-group">
                    <input
                        className="entrada"
                        id="envio" 
                        type="checkbox"
                        name="envio"
                        checked={envio}
                        onChange={onChange}
                    />
                    <label htmlFor="envio">envío</label>
                </div>

                <button disabled={!valido} className={editarID ? "btnActualizar" : "btnAgregar"}>
                    {editarID ? 'Actualizar' : 'Agregar'}
                </button>
            </form>       
    );
}