export const Card = props => {
    const { producto, agregarCarritoID } = props

    return (
        <section>
            <h3>{producto.nombre}</h3>
            <img src={producto.foto + '?' + (1 || Math.random())} alt="" />
            <p><b>Precio: </b>${producto.precio}</p>
            <p><b>Stock: </b>{producto.stock}</p>
            <p><b>Marca: </b>{producto.marca}</p>
            <p><b>Categoría: </b>{producto.categoria}</p>
            <p><b>Detalles: </b>{producto.descripcionLarga}</p>
            <br />
            <p><b style={{ color: 'gold' }}>Envío: </b>{producto.envio ? 'Si' : 'No'}</p>
            <button id={"btnComprar-" + producto.id} onClick={
                () => agregarCarritoID(producto.id)
            }>Agregar al carrito</button>

            {/* <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button id="btnClose" type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            Producto agregado al carrito
                        </div>
                        <div className="modal-footer">
                            <button id="btnModal" type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                        </div>
                    </div>
                </div>
            </div> */}

        </section>
    )
}