import axios from "axios"

console.warn('process.env.NODE_ENV:', process.env.NODE_ENV)

const url = process.env.NODE_ENV === 'production'
            ? '/api/productos/'                         // en producción
            : `http://localhost:${process.env.REACT_APP_PORT_SRV_DEV}/api/productos/`   // en desarrollo


export const proxyProducto = producto => {
    const handler = {
        get: function(target, prop, receiver) {
            if(prop === 'id') prop = '_id'

            return target[prop]
        }
    }

    return new Proxy(producto, handler)
}

const eliminarPropiedad = (obj, prop) => {
    const objClon = {...obj}
    delete objClon[prop]
    return objClon
}

export const getAll = async _ => (await axios.get(url)).data.map(p => proxyProducto(p))
export const guardar = async prod => proxyProducto((await axios.post(url, prod)).data)
export const actualizar = async (id, prod) => proxyProducto((await axios.put(url+id, eliminarPropiedad(prod, '_id'))).data)
export const eliminar = async id => proxyProducto((await axios.delete(url+id)).data)
